.searchInputs {
    margin-top: 25px;
    display: flex;
}

.search Input {
border-radius:  2px;

font-size: 18px;
padding: 15px;
height: 30px;
width: 300px;
}

.searchIcon {
    height: 60px;
    width: 50px;
    background-color: white;
   
   place-items: center;
}

.searchIcon svg {
font-size:  35px;
}

.dataResult {
    margin-top: 5px;
    width:  300px;
    height: 200px;
    background-color: white;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
    display:  none;
}

.dataResult .dataItem {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
}

.dataItem p{
    margin-left: 10px;
}

Link:hover{
background-color: orangered;
}

#clearBtn{
    cursor: pointer;
}